// components/ChatInput.tsx
import React from 'react';
import './widget.css'; // Assuming separate CSS for styling

interface ChatInputProps {
    value: string;
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void; // Correct typing for onChange
    onSubmit: () => void;
  }

const ChatInput: React.FC<ChatInputProps> = ({ value, onChange, onSubmit }) => {
  // Handler for input change
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    onChange(e);
  };

  // Handler for key down (to submit on Enter)
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      onSubmit();
    }
  };

  return (
    <div className="chat_input_container">
      <input
        type="text"
        value={value}
        onChange={handleChange}
        onKeyDown={handleKeyDown}
        className="input-field"
        placeholder="Skriv en besked"
      />
      <button onClick={onSubmit} className="send_button">
        Send
      </button>
    </div>
  );
};

export default ChatInput;
