// /widget/widget.js

import React from 'react';
import ReactDOM from 'react-dom';
import StreamedTextField from '../src/components/StreamedTextField';
import "../src/components/widget.css";

function loadWidgetStylesheet() {
    const link = document.createElement('link');
    link.href = 'https://widget.creativeoak.dk/ongrass/widget.css'; // Make sure to provide the correct path
    link.type = 'text/css';
    link.rel = 'stylesheet';
    document.head.appendChild(link);
}

function isAllowedDomain() {
    // List of allowed domains
    const allowedDomains = ['https://www.creativeoak.dk'];
    // Checking the current page's location against the allowed domains
    return allowedDomains.some(domain => window.location.href.startsWith(domain));
}

window.renderChatWidget = (containerId) => {
    if (!isAllowedDomain()) {
        console.error("This domain is not authorized to use the chat widget.");
        return; // Exit the function to prevent the widget from loading
    }

    loadWidgetStylesheet();
    ReactDOM.render(<div className='chat_parent'><StreamedTextField /></div>, document.getElementById(containerId));
};
