import React, { useState } from "react";
import ChatHeader from "../ChatHeader";
import ChatInput from "../ChatInput";
import MessageList from "../MessageList";
import useChat from "../../hooks/useChat";
import "../widget.css";
import ClickChatButton from "../ClickChatButton";

const StreamedTextField: React.FC = () => {
  const [showChat, setShowChat] = useState(false);
  const {
    chatMessages,
    sendMessage,
    inputValue,
    setInputValue,
    showLoading,
    streamingText,

  } = useChat();

  const handleSubmit = () => {
    sendMessage(inputValue); // Call sendMessage with the current input value
  };

  const updateShowChat = (show:boolean) =>{
    setShowChat(show)
  }

  return (
    <div className="inside_chat_parent">
      <ClickChatButton
        setShowFunction={updateShowChat}
        showChat={showChat}
      />
      {showChat && (
        <div className="chat_area chat-visible">
          <ChatHeader
            onClick={() => setShowChat(!showChat)}
            showChat={showChat}
          />
          <MessageList
            messages={chatMessages}
            loading={showLoading}
            streamingText={streamingText}
            errorMessage={""}
          />
          <ChatInput
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onSubmit={handleSubmit} // Pass the wrapped function here
          />
        </div>
      )}
    </div>
  );
};

export default StreamedTextField;
