import { useState, useEffect } from "react";
import { ChatMessage } from "../types/ChatMessage"; // Ensure this path matches your project structure

const useChat = () => {
  const default_message: ChatMessage = {
    content:
      "Velkommen til! Jeg hedder Foodie, og er her for at assistere og vejlede de frivillige i On Grass Solutions før, under og efter festivalen. Jeg kan give information om alt fra påklædning og udstyr, arbejdsplaner, løn og godtgørelser, til oplæring og forberedelse.",
    role: "bot",
  };
  const [chatMessages, setChatMessages] = useState<ChatMessage[]>([
    default_message,
  ]);
  const [inputValue, setInputValue] = useState("");
  const [showLoading, setShowLoading] = useState(false);
  const [threadID, setThreadID] = useState("");
  const [streamingText, setStreamingText] = useState("");

  // Define the backend URL from your environment variables or directly
  const connectUrl =
    process.env.REACT_APP_ASK_URL ||
    "https://chatbackend-xsl6lnma6a-ew.a.run.app";

  const createThread = async () => {
    setShowLoading(true);
    try {
      const response = await fetch(`${connectUrl}/create_thread`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();

      return data.thread_id; // Adjust according to the actual response
    } catch (error) {
      console.error("Failed to create thread:", error);
    }
    setShowLoading(false);
  };

  const sendMessage = async (message: string) => {
    // Assuming default initial state for threadID is something invalid for proceeding operations, like null or empty string.
    let currentThreadID = threadID;

    if (chatMessages.length === 1 && !currentThreadID) {
      const thread = await createThread();

      currentThreadID = thread; // Adjust this based on your actual API response

      setThreadID(currentThreadID); // Upda
    }

    console.log(currentThreadID);
    const tempMessage: ChatMessage = {
      content: inputValue,
      role: "User",
    };
    if (currentThreadID) {
      setChatMessages((currentMessages) => [...currentMessages, tempMessage]); // Optionally add the input value to chat
      setStreamingText(""); // Clear streaming text for new input
      setInputValue(""); // Clear the input field
      setShowLoading(true); // Show the loading spinner

      try {
        const response = await fetch(connectUrl + "/ask", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            question: inputValue,
            context: chatMessages,
            threadID: threadID,
          }),
        });

        if (response.body === null) throw new Error("Response body is null");

        const reader = response.body.getReader();

        // Stream processing
        const stream = new ReadableStream({
          async start(controller) {
            let localStreamingText = ""; // Accumulate text here

            while (true) {
              const { done, value } = await reader.read(); // Read the next chunk of data
              if (done) {
                let fullMessage: ChatMessage;
                try {
                  // Validate the presence and type of necessary metadata properties

                  fullMessage = {
                    content: localStreamingText, // assuming localStreamingText is previously defined and valid

                    role: "bot",
                  };

                  // Proceed with using fullMessage as needed
                } catch (error) {
                  console.error("Error constructing ChatMessage:", error);
                  fullMessage = {
                    content: localStreamingText,

                    role: "bot",

                    // Handle the error appropriately
                    // This might include logging the error, notifying a user, etc.
                  };
                }
                //When done, append the full message to chatMessages
                setChatMessages((currentMessages) => [
                  ...currentMessages,
                  fullMessage,
                ]);

                setStreamingText(""); // Clear streaming text
                break;
              }

              // Convert the Uint8Array to string
              let textChunk = new TextDecoder().decode(value);

              localStreamingText += textChunk; // Accumulate the text chunk

              if (setShowLoading) {
                setShowLoading(false); // Hide the loading spinner
              }
              setStreamingText((currentText) => currentText + textChunk); // Update temporary streaming text

              controller.enqueue(value);
            }

            controller.close();
            reader.releaseLock();
          },
        });

        await new Response(stream).text();
      } catch (error) {
        console.error("Error fetching data:", error);
        setShowLoading(false);
      }
    } else {
      console.log("HELLO");
    }
  };

  // Resets threadID and chat messages
  const resetChat = () => {
    setThreadID("");
    setChatMessages([]);
  };

  // Initialization (if needed)
  useEffect(() => {
    // Place any setup code here, e.g., automatically creating a thread at start
  }, []);

  return {
    chatMessages,
    sendMessage,
    inputValue,
    setInputValue,
    showLoading,
    resetChat,
    streamingText,
  };
};

export default useChat;
