// components/MessageItem.tsx
import React from "react";
import { ChatMessage } from "../types/ChatMessage"; // Assuming you've defined your types elsewhere
import formatContent from "../utils/formatContent"; // Assuming you have a utility function for formatting

interface MessageItemProps {
  message: ChatMessage;
}

const MessageItem: React.FC<MessageItemProps> = ({ message }) => {
  const formattedMessage = formatContent(message.content);

  return (
    <div
      style={{ textAlign: message.role === "bot" ? "left" : "right" }}
      className={`message_item ${
        message.role === "bot" ? "bot_message" : "user_message"
      }`}
    >
      <p className="message_author">
        {message.role === "bot" ? "Foodie" : "Dig"}
      </p>
      <div className="message_content">{formattedMessage}</div>
    </div>
  );
};

export default MessageItem;
