// components/MessageList.tsx
import React from "react";
import MessageItem from "./MessageItem";
import LoadingGif from "../assets/chat_loading.gif"; // Adjust path as necessary
import BackgroundImage from "../assets/bg.png";
import { ChatMessage } from "../types/ChatMessage"; // Assuming you've defined your types elsewhere

interface MessageListProps {
  messages: ChatMessage[];
  loading: boolean;
  streamingText: string;
  errorMessage: string;
}

const MessageList: React.FC<MessageListProps> = ({
  messages,
  loading,
  streamingText,
  errorMessage,
}) => {
  return (
    <div>
      <div className="chat_container">
        {messages.map((message, index) => (
          <MessageItem key={index} message={message} />
        ))}
        {loading && (
          <div className="loading_container">
            <img src={LoadingGif} alt="Loading..." />
          </div>
        )}
        {streamingText && (
          <MessageItem message={{ content: streamingText, role: "bot" }} />
        )}
        {errorMessage && <div className="error_message">{errorMessage}</div>}
      </div>
      <div className="bg-chat-image">
        <img alt="a field of green" src={BackgroundImage} />
      </div>
    </div>
  );
};

export default MessageList;
