// utils/formatContent.tsx
import React from "react";
import sanitizeHtml from "sanitize-html";
import * as linkify from "linkifyjs";
import linkifyHtml from "linkify-html";

const formatContent = (content: string): JSX.Element => {
  // First, replace markdown-style bold syntax with HTML <strong> tags
  let formattedContent = content
    .replace(/\n/g, "<br/>") // Replace newlines with <br/>
    .replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>"); // Replace **text** with <strong>text</strong>

  // Next, sanitize the HTML to prevent XSS attacks
  const sanitizedContent = sanitizeHtml(formattedContent, {
    allowedTags: sanitizeHtml.defaults.allowedTags.concat(["strong", "br"]), // Allow <strong> and <br/> tags
    allowedAttributes: {}, // No attributes allowed
  });

  const linkifiedContent = linkifyHtml(sanitizedContent);

  // Finally, wrap the content in a Linkify component to auto-detect and link URLs
  return <div dangerouslySetInnerHTML={{ __html: linkifiedContent }} />;
};

export default formatContent;
