// components/ChatHeader.tsx
import React from 'react';
import ChatbotIcon from '../assets/chatbot_icon.png'; // Adjust path as necessary
import './widget.css'; // Assuming separate CSS for styling

interface ChatHeaderProps {
  onClick: () => void; // Function to handle showing/hiding the chat
  showChat: boolean;
}

const ChatHeader: React.FC<ChatHeaderProps> = ({ onClick, showChat }) => {
  return (
    <div className="chat_header">
      <img src={ChatbotIcon} alt="Chatbot Icon" className="header_image" />
      <p className='chat_name'>Foodie</p>
      <div className="header_buttons">
        <button className="close-button" onClick={onClick}>
          {showChat ? 'X' : ''}
        </button>
      </div>
    </div>
  );
};

export default ChatHeader;
