import React from "react";
import ChatbotIcon from "../assets/chatbot_icon.png"; // Adjust path as necessary
import "./widget.css"; // Assuming separate CSS for styling

interface ClickChatButtonProps {
  setShowFunction: (show: boolean) => void; // Function to handle showing/hiding the chat
  showChat: boolean;
}

const ClickChatButton: React.FC<ClickChatButtonProps> = ({ setShowFunction, showChat }) => {
  return (
    <div onClick={() => {
        setShowFunction(!showChat)
    }} className="chatbot_logo_click">
      <img src={ChatbotIcon} alt="" style={{ width: "100%" }} />
    </div>
  );
};

export default ClickChatButton;
